.modal {
  position:fixed;
  top:100px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.7);
}

.modal ul {
  position: absolute;
  right: 0;
  background-color: rgb(255, 255, 255, 0.95);
  display: flex;
  flex-direction: column;
  width: fit-content;
}

.modal li {
  margin: 0;
}

.modal li + li {
  border-top: 1px solid rgb(175, 175, 175);
}

.button {
  border: 0;
  background-color: transparent;
  cursor: pointer;
  width: 300px;
  height: 100px;
  margin: 0;
  font-size: 22px;
  font-weight: bold;
}
