.container {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  min-height: 250px;
  max-height: 400px;
  height: 40vh;
}

.contact-icons img{
  margin: 0 2em;
  animation: contact-icons 1s linear;
  width: 128px;
}

.contact-icons img:hover {
  transform: scale(1.1);
}

@media (max-width: 800px) {
  .container {
    width: 100%;
  }
}

@media (max-width: 576px) {
  .container {
    max-height: none;
    height: fit-content;
  }

  .contact-icons {
    display: flex;
    flex-direction: column;
  }

  .contact-icons img{
    margin: 1em 0;
    animation: contact-icons 1s linear;
    width: 100px;
  }
}

@keyframes contact-icons {
  0%{
    transform: translateY(-100px);
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}